body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  background-color: #eae8f4;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
  transition-property: transform;
  transition-duration: 0.3s;
}
img:hover {
  transform: scale(1.1);
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.bg-negro {
  background-color: #000;
}

.header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3)), url("../img/header_bg.jpg");
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}
.header__texto {
  padding: 7rem 0 15rem 0;
  text-align: center;
  color: #e2e27e;
}

.navegacion {
  padding: 1.5rem 5rem;
  background-color: #4b4083;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .navegacion__contenido {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-bottom: 0rem;
  }
}
.navegacion__enlace:hover {
  color: #e2e27e;
}
.navegacion__enlace--activa {
  font-weight: 900;
  color: #e2e27e;
  border-bottom: 1px solid #e2e27e;
}

.contacto {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
}
.contacto__medio {
  width: 3rem;
}

.sobre-mi {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -10rem;
}
.sobre-mi__imagen {
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
}
.sobre-mi__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
}
.sobre-mi__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
.sobre-mi__texto {
  max-width: 90rem;
  text-align: center;
  font-size: 2.2rem;
}
.sobre-mi__texto::before {
  content: '"';
  color: #4b4083;
  font-size: 4rem;
}

.estudios__contenido {
  background-color: #4b4083;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.estudios__contenido--principal {
  background-color: transparent;
}
.estudios__heading {
  text-transform: uppercase;
  color: #e2e27e;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
}
.estudios__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #e2e27e;
  margin: 0 auto;
}
.estudios__grid {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .estudios__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.estudios__enlace {
  text-transform: uppercase;
  border: 2px solid #e2e27e;
  padding: 1rem 5rem;
  font-weight: 700;
  color: #e2e27e;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.estudios__enlace:hover {
  background-color: #e2e27e;
  color: #4b4083;
}

.estudio__imagen {
  width: 100%;
  height: 30rem;
}
.estudio__contenido {
  background-color: #FFFFFF;
  padding: 2rem;
  text-align: center;
  border-end-end-radius: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .estudio__contenido {
    min-height: 33rem;
  }
}
.estudio__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
}
.estudio__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
.estudio__institucion {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 2rem;
}
.estudio__fecha, .estudio__estado {
  font-weight: 700;
}
.estudio__pro {
  order: -1;
}
@media (min-width: 768px) {
  .estudio__pro {
    order: 0;
  }
}
.estudio__enlace {
  order: 1;
}

.estudios-informales {
  margin-block: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.estudios-informales__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
  margin-bottom: 2rem;
}
.estudios-informales__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .estudios-informales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.estudio-informal {
  border: 1px solid #4b4083;
  border-radius: 2rem;
  padding: 2rem;
  text-align: center;
  margin-bottom: 2rem;
}
.estudio-informal:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .estudio-informal {
    margin-bottom: 0;
  }
}
.estudio-informal__titulo {
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #4b4083;
}
.estudio-informal__tipo, .estudio-informal__estado, .estudio-informal__institucion {
  font-size: 2rem;
}

.habilidades {
  margin-block: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.habilidades__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
}
.habilidades__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
.habilidad__tipo {
  text-transform: uppercase;
  color: #d4d440;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
  margin: 2.5rem 0;
}
.habilidad__tipo::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #d4d440;
  margin: 0 auto;
}
.habilidad__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .habilidad__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.habilidad__imagen {
  width: 18rem;
  height: 18rem;
}
.habilidad__texto {
  font-weight: 700;
  font-size: 2.2rem;
  text-transform: uppercase;
  color: #4b4083;
  margin: 1rem 0;
}
.habilidad__elemento {
  font-size: 2rem;
}

.contactame {
  margin-top: 5rem;
  padding-top: 3rem;
  background-color: #e2e27e;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contactame__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
}
.contactame__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
.contactame__grid {
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .contactame__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.contactame__contenido {
  margin-bottom: 2rem;
}
.contactame__imagen {
  width: 15rem;
  height: 15rem;
  margin: 0 auto;
}
.contactame__texto {
  text-align: center;
  font-weight: 700;
  color: #4b4083;
}

.proyectos {
  margin-block: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.proyectos__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
}
.proyectos__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .proyectos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.proyectos__tipo {
  text-transform: uppercase;
  color: #e2e27e;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
  margin: 2.5rem;
}
.proyectos__tipo::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #e2e27e;
  margin: 0 auto;
}

.proyecto__grid {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
  text-align: center;
  border: 2px solid #bbb4db;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .proyecto__grid {
    margin-top: 0rem;
  }
}
.proyecto__imagen {
  height: 15rem;
  width: 15rem;
}
.proyecto__contenido {
  min-width: 15rem;
  padding: 1rem;
  margin: 0 auto;
}
.proyecto__texto {
  font-weight: 700;
  font-size: 2rem;
}
.proyecto__pagina {
  font-weight: 700;
  padding: 1rem 3rem;
  border: 2px solid #4b4083;
  display: inline-block;
  margin-top: 1rem;
  color: #4b4083;
}
.proyecto__pagina:hover {
  border: 2px solid #e2e27e;
}

.experiencias {
  margin-block: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.experiencias__heading {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
  margin-bottom: 2rem;
}
.experiencias__heading::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .experiencias__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.experiencias__tipo {
  text-transform: uppercase;
  color: #e2e27e;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
  margin: 2.5rem;
}
.experiencias__tipo::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #e2e27e;
  margin: 0 auto;
}
.experiencias__enlace {
  font-weight: 700;
  padding: 1rem 3rem;
  border: 2px solid #4b4083;
  display: inline-block;
  margin-top: 1rem;
  color: #4b4083;
}
.experiencias__enlace:hover {
  border: 2px solid #e2e27e;
}

.experiencia__grid {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
  text-align: center;
  border: 2px solid #bbb4db;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .experiencia__grid {
    margin-top: 0rem;
  }
}
.experiencia__imagen {
  height: 15rem;
  width: 15rem;
}
.experiencia__contenido {
  min-width: 15rem;
  padding: 1rem;
  margin: 0 auto;
}
.experiencia__texto {
  font-weight: 700;
  font-size: 2rem;
}
.experiencia__empresa {
  font-size: 2.5rem;
  font-weight: 700;
}
.experiencia__cargo {
  font-size: 2rem;
  font-weight: 700;
  color: #4b4083;
}
.experiencia__enlace {
  font-weight: 700;
  padding: 1rem 3rem;
  border: 2px solid #4b4083;
  display: inline-block;
  margin-top: 1rem;
  color: #4b4083;
}
.experiencia__enlace:hover {
  border: 2px solid #e2e27e;
}

.laboral {
  margin-block: 5rem;
}
.laboral__contenedor {
  margin-bottom: 2rem;
}
.laboral__contenedor:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .laboral__contenedor {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
  .laboral__contenedor:nth-child(even) .laboral__texto {
    grid-column: 1/3;
    order: -1;
  }
}
.laboral__texto {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .laboral__texto {
    grid-column: 2/4;
  }
}
.laboral__imagen {
  width: 100%;
}
.laboral__cargo {
  text-transform: uppercase;
  color: #4b4083;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  display: block;
  font-size: 2rem;
}
.laboral__cargo::after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  background-color: #4b4083;
  margin: 0 auto;
}
.laboral__empresa, .laboral__tiempo {
  font-weight: 700;
  margin: 0.5rem;
  font-size: 1.8rem;
}
.laboral__funciones {
  font-size: 1.8;
}
.laboral__logro {
  font-weight: 700;
  text-decoration: underline #4b4083;
}

.footer {
  background-color: #4b4083;
  padding: 10rem 0;
}
.footer__grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__grid {
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }
}